import uniNavBar from '../../components/uni-nav-bar/uni-nav-bar.vue'
import UploadImage from '../../components/common/upload-image.vue'
import uniPopup from '../../components/uni-popup/uni-popup.vue'

let changLookText = ['所有人可见', '仅自己可见']	
export default {
	components: {
		uniNavBar,
		UploadImage,
		uniPopup
	},
	data() {
		return {				
			sercet: '所有人可见',
			text: '',
			imageList: [],
			isSave: false // 是否弹出了保存草稿
		}
	},
	// 监听返回行为
	onBackPress() {
		if(this.text == '' && this.imageList.length == 0) return
		if(!this.isSave) {
			this.save()
			return true
		}			
	},
	onLoad() {
		// 在ios上 需要这么解决(因为默认给show不显示)
		setTimeout(() => {
			this.open()
		}, 0)			
	},
	methods: {
		// 返回
		back() {
			uni.navigateBack()
		},
		// 发布
		submit() {
			console.log('发布')
		},
		// 隐私
		changeLook() {				
			uni.showActionSheet({
				itemList: changLookText,
				success: (res) => {
					this.sercet = changLookText[res.tapIndex]
				}
			})
		},
		upload(arr) {
			this.imageList = arr
			console.log(this.imageList)
		},
		change(e) {
			console.log('e', e)
		},
		// 打开弹窗
		open(){
		    this.$refs.popup.open()
		},
		// 关闭弹窗
		close() {
			this.$refs.popup.close()
		},
		// 保存草稿逻辑
		save() {
			uni.showModal({
				content: '是否需要保存为草稿？',
				cancelText: '不保存',
				confirmText: '保存',
				success: res => {					
					if(res.confirm) { // 保存
						
					} else { // 不保存
					
					}
					this.isSave = true
					uni.navigateBack({
						delta: 1
					})						
				}
			})
		}
	}
}
